.profile__cont {
	position: relative;
	max-width: 120px;
	width: 100%;
	height: 120px;
	margin-right: 30px;
}

.profile__cont:hover .profile__avatar {
	opacity: .2;
}

.profile__cont:hover .profile__icon {
	opacity: 1;
	visibility: visible;
}

.profile__cont:hover {
	cursor: pointer;
}