@import url(./__container/popup__container.css);
@import url(./__close/popup__close.css);


.popup {
  opacity: 0;
  visibility: hidden;
  transition: .55s opacity, .55s visibility;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
